:root {
  --shadow1: #3c2d80;
  --shadow2: rgba(38, 55, 129, 0.664);
}

.pinGeneratorContainer {
  width: 430px;
  min-height: 500px;
  padding: 60px 35px 35px 35px;
  border-radius: 30px;
  background: linear-gradient(
    to right bottom,
    rgba(67, 42, 211, 0.3),
    rgba(6, 74, 163, 0.3)
  );
  backdrop-filter: blur(15px);
  position: relative;
  z-index: 2;
}

.logo {
  background-image: url("https://www.pinclipart.com/picdir/middle/126-1262958_ahmad-ghozali-on-twitter-chit-chat-clip-art.png");
  width: 100px;
  height: 100px;
  background-size: contain;
  margin: 0 auto;
  border-radius: 50px;
  box-shadow: 8px 8px 8px var(--shadow2), -8px 2px 8px var(--shadow1),
    0px 0px 2px black, 0px 0px 15px var(--shadow1);
}

.title {
  text-align: center;
  font-size: 24px;
  padding-top: 24px;
  letter-spacing: 0.5px;
  color: #fff;
}

.explanation {
  font-size: 14px;
  padding: 20px 10px 10px 5px;
  color: #fff;
}

.inputFields {
  padding: 5px 5px 5px 5px;
}

.inputFields svg {
  height: 20px;
  margin: 0 0px -3px 25px;
  padding: 22px 0px 20px 5px;
}

.userInput {
  width: 100%;
  border: none;
  outline: none;
  background: none;
  font-size: 20px;
  color: rgb(255, 255, 255);
  padding: 20px 10px 20px 5px;
}

::placeholder {
  color: rgb(255, 255, 255);
  opacity: 0.4;
}

.username {
  display: flex;
  margin-bottom: 10px;
  border-radius: 10px;
  box-shadow: inset 8px 8px 8px var(--shadow1),
    inset -8px -8px 8px var(--shadow2);
}

.button {
  margin-bottom: 10px;
  outline: none;
  border: none;
  cursor: pointer;
  width: 100%;
  height: 60px;
  border-radius: 10px;
  font-size: 20px;
  font-weight: 700;
  color: rgb(255, 255, 255);
  text-align: center;
  background-color: rgba(17, 231, 63, 0.705);
  transition: all 0.1s ease-in;
}

.button:hover {
  background-color: #dd34ff;
}

.button:active {
  background-color: rgb(81, 133, 206);
}

.pin {
  text-align: center;
  font-size: 14px;
  margin-bottom: 10px;
  letter-spacing: 0.5px;
}

.userData {
  padding: 20px;
  margin-bottom: 10px;
  border-radius: 10px;
  box-shadow: 8px 8px 8px var(--shadow1), -8px -8px 8px var(--shadow2);
}

@media (max-width: 500px) {
  .pinGeneratorContainer {
    width: 100vw;
    min-height: 100vh;
    padding-bottom: 0;
    padding-top: 0;
    border-radius: 0;
  }

  .logo {
    margin-top: 50px;
  }
}
